import axios, { AxiosInstance, AxiosResponse, AxiosError } from "axios";
import { ApiError, ApiResponse } from "../types";
import { getFullPath } from "../utils/router";

export const API_VERSION = process.env.REACT_APP_API_VERSION || "v1";
const API_OVERRIDE_STORAGE_KEY = "trafficApiBaseUrlOverride";
const API_OVERRIDE_QUERY_KEYS = ["apiBaseUrl", "apiBase", "api"];

const normalizeBaseUrl = (value: string): string => value.replace(/\/+$/, "");

const isValidApiBaseUrl = (value: string): boolean => {
  try {
    const url = new URL(value);
    if (url.protocol !== "https:" && !["localhost", "127.0.0.1"].includes(url.hostname)) {
      return false;
    }
    return url.protocol === "https:" || url.protocol === "http:";
  } catch {
    return false;
  }
};

const persistOverride = (value: string): void => {
  if (typeof window === "undefined") {
    return;
  }
  try {
    window.localStorage?.setItem(API_OVERRIDE_STORAGE_KEY, value);
  } catch (error) {
    console.warn("Failed to persist API override:", error);
  }
};

const removeOverride = (): void => {
  if (typeof window === "undefined") {
    return;
  }
  try {
    window.localStorage?.removeItem(API_OVERRIDE_STORAGE_KEY);
  } catch (error) {
    console.warn("Failed to clear API override:", error);
  }
};

const getStoredOverride = (): string | null => {
  if (typeof window === "undefined") {
    return null;
  }
  try {
    const stored = window.localStorage?.getItem(API_OVERRIDE_STORAGE_KEY);
    if (stored && isValidApiBaseUrl(stored)) {
      return stored;
    }
    if (stored) {
      window.localStorage?.removeItem(API_OVERRIDE_STORAGE_KEY);
    }
  } catch (error) {
    console.warn("Failed to read API override:", error);
  }
  return null;
};

const getQueryOverride = (): string | null => {
  if (typeof window === "undefined") {
    return null;
  }
  try {
    const params = new URLSearchParams(window.location.search);
    for (const key of API_OVERRIDE_QUERY_KEYS) {
      const candidate = params.get(key);
      if (candidate && isValidApiBaseUrl(candidate)) {
        const normalized = normalizeBaseUrl(candidate);
        persistOverride(normalized);
        const url = new URL(window.location.href);
        params.delete(key);
        url.search = params.toString();
        window.history.replaceState({}, document.title, url.toString());
        return normalized;
      }
    }
  } catch (error) {
    console.warn("Failed to parse query override:", error);
  }
  return null;
};

const resolveRuntimeOverride = (): string | null => {
  const queryOverride = getQueryOverride();
  if (queryOverride) {
    return queryOverride;
  }
  const storedOverride = getStoredOverride();
  if (storedOverride) {
    return storedOverride;
  }
  return null;
};

/**
 * * Determines the API base URL based on environment
 * * Priority:
 * * 1. REACT_APP_API_URL environment variable (highest priority)
 * * 2. Auto-detect based on current hostname
 * * 3. Default to localhost for development
 */
const getApiBaseUrl = (): string | null => {
  const runtimeOverride = resolveRuntimeOverride();
  if (runtimeOverride) {
    return normalizeBaseUrl(runtimeOverride);
  }

  if (process.env.REACT_APP_API_URL) {
    const envUrl = normalizeBaseUrl(process.env.REACT_APP_API_URL);
    if (isValidApiBaseUrl(envUrl)) {
      return envUrl;
    }
    console.warn("Invalid REACT_APP_API_URL detected. Falling back to auto-detection.");
  }

  // * Auto-detect based on current location (client-side only)
  // * This takes priority over environment variable for localhost development
  if (typeof window !== "undefined") {
    const hostname = window.location.hostname;
    const isLocalhost = hostname === "localhost" || hostname === "127.0.0.1";
    const isGitHubPages = hostname.includes("github.io");

    // * Development: localhost or 127.0.0.1 - ALWAYS use localhost:5000
    if (isLocalhost) {
      return "http://localhost:5000";
    }

    // * Production: GitHub Pages or other production domains
    if (isGitHubPages) {
      console.error(
        "No API base URL configured for GitHub Pages deployment. Set REACT_APP_API_URL during build or use ?apiBaseUrl=YOUR_URL to override at runtime."
      );
      return null;
    }

    // * Other production domains: use environment variable or same origin
    return window.location.origin;
  }

  // * Server-side rendering fallback
  return "http://localhost:5000";
};

// Create axios instance
// * API versioning: Currently using v1 endpoints
// * Backward compatibility: Non-versioned endpoints still work but are deprecated
let apiBaseUrl = getApiBaseUrl();

const getVersionedBaseUrl = (baseUrl: string | null): string => {
  if (!baseUrl) {
    return `/api/${API_VERSION}`;
  }
  return `${normalizeBaseUrl(baseUrl)}/api/${API_VERSION}`;
};

const api: AxiosInstance = axios.create({
  baseURL: getVersionedBaseUrl(apiBaseUrl),
  timeout: 30000, // * Increased timeout to 30 seconds
  headers: {
    "Content-Type": "application/json",
  },
});

const applyBaseUrlToClient = (baseUrl: string | null): void => {
  api.defaults.baseURL = getVersionedBaseUrl(baseUrl);
};

export const getResolvedApiBaseUrl = (): string | null => apiBaseUrl;

export const getApiBaseUrlOverride = (): string | null => getStoredOverride();

export const setApiBaseUrlOverride = (value: string): boolean => {
  if (!isValidApiBaseUrl(value)) {
    return false;
  }
  const normalized = normalizeBaseUrl(value);
  persistOverride(normalized);
  apiBaseUrl = normalized;
  csrfToken = null;
  applyBaseUrlToClient(apiBaseUrl);
  return true;
};

export const clearApiBaseUrlOverride = (): void => {
  removeOverride();
  apiBaseUrl = getApiBaseUrl();
  csrfToken = null;
  applyBaseUrlToClient(apiBaseUrl);
};

/**
 * * Normalizes API responses so components can depend on a consistent shape
 */
export const unwrapApiResponse = <T>(
  payload: ApiResponse<T> | { data?: T } | T | null | undefined
): T | null => {
  if (!payload) {
    return null;
  }

  if (typeof payload === "object" && "data" in payload && (payload as { data?: T }).data !== undefined) {
    return (payload as { data?: T }).data as T;
  }

  if (typeof payload === "object" && "success" in payload) {
    const response = payload as ApiResponse<T>;
    return response.data ?? null;
  }

  return payload as T;
};

declare global {
  interface Window {
    trafficApiConfig?: {
      setApiBaseUrlOverride: (value: string) => boolean;
      clearApiBaseUrlOverride: () => void;
      getApiBaseUrlOverride: () => string | null;
    };
  }
}

if (typeof window !== "undefined") {
  window.trafficApiConfig = {
    setApiBaseUrlOverride,
    clearApiBaseUrlOverride,
    getApiBaseUrlOverride,
  };
}


// * CSRF token storage (in-memory, refreshed on each response)
let csrfToken: string | null = null;

/**
 * Fetch CSRF token from server
 * Makes a GET request to get a fresh CSRF token
 * Uses getCurrentUser as it's a simple authenticated endpoint
 */
export const fetchCSRFToken = async (): Promise<string | null> => {
  // * If we already have a token, return it
  if (csrfToken) {
    return csrfToken;
  }
  
  try {
    // * Use getCurrentUser to get CSRF token from response header
    const response = await api.get('/auth/me');
    const token = response.headers['x-csrf-token'] || response.headers['X-CSRF-Token'];
    if (token) {
      csrfToken = token;
      return token;
    }
  } catch (error) {
    // * If /auth/me fails, try dashboard stats as fallback
    try {
      const response = await api.get('/dashboard/stats');
      const token = response.headers['x-csrf-token'] || response.headers['X-CSRF-Token'];
      if (token) {
        csrfToken = token;
        return token;
      }
    } catch (fallbackError) {
      console.warn('Failed to fetch CSRF token from both endpoints:', error);
    }
  }
  return csrfToken; // Return existing token if fetch fails
};

// Request interceptor to add auth token and CSRF token
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    
    // * Add CSRF token for state-changing requests (POST, PUT, DELETE, PATCH)
    if (csrfToken && ['post', 'put', 'delete', 'patch'].includes(config.method?.toLowerCase() || '')) {
      config.headers['X-CSRF-Token'] = csrfToken;
    }
    
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Response interceptor to handle errors and extract CSRF token
api.interceptors.response.use(
  (response: AxiosResponse) => {
    // * Extract CSRF token from response header if present
    const newCsrfToken = response.headers['x-csrf-token'] || response.headers['X-CSRF-Token'];
    if (newCsrfToken) {
      csrfToken = newCsrfToken;
    }
    return response;
  },
  async (error: AxiosError<ApiError>) => {
    const originalRequest = error.config;
    
    // Handle 401 errors (unauthorized)
    if (error.response?.status === 401 && originalRequest) {
      // * Skip token refresh/redirect for auth endpoints (login, register, etc.)
      // * These endpoints return 401 for invalid credentials, not expired tokens
      const requestUrl = originalRequest.url || '';
      const isAuthEndpoint = 
        requestUrl.includes('/auth/login') ||
        requestUrl.includes('/auth/register') ||
        requestUrl.includes('/auth/forgot-password') ||
        requestUrl.includes('/auth/reset-password') ||
        requestUrl.includes('/auth/verify-email');
      
      // * If this is an auth endpoint, just reject the error normally
      // * The login/register page will handle the error and show the message
      if (isAuthEndpoint) {
        return Promise.reject(error);
      }
      
      // * For other endpoints, try to refresh the token
      try {
        const refreshToken = typeof window !== 'undefined' && window.localStorage 
          ? localStorage.getItem('refreshToken') 
          : null;
      
        if (refreshToken) {
          try {
            // * Use versioned endpoint for token refresh
            const refreshUrl = `${getVersionedBaseUrl(apiBaseUrl)}/auth/refresh`;
            const response = await axios.post(refreshUrl, {
              refreshToken,
            });
            
            const { token } = response.data.data;
            if (typeof window !== 'undefined' && window.localStorage) {
              localStorage.setItem('token', token);
            }
            
            // Retry original request
            originalRequest.headers.Authorization = `Bearer ${token}`;
            return api(originalRequest);
          } catch (refreshError) {
            // Refresh token failed, redirect to login
            if (typeof window !== 'undefined' && window.localStorage) {
              localStorage.removeItem('token');
              localStorage.removeItem('refreshToken');
              localStorage.removeItem('user');
            }
            // * Redirect to login with proper basename
            window.location.href = getFullPath('/login');
          }
        } else {
          // No refresh token, redirect to login
          if (typeof window !== 'undefined' && window.localStorage) {
            localStorage.removeItem('token');
            localStorage.removeItem('user');
          }
          // * Redirect to login with proper basename
          window.location.href = getFullPath('/login');
        }
      } catch {
        // If localStorage access fails, just redirect to login
        if (typeof window !== 'undefined') {
          // * Redirect to login with proper basename
          window.location.href = getFullPath('/login');
        }
      }
    }
    
    return Promise.reject(error);
  }
);

// API service class
class ApiService {
  // Auth endpoints
  async login(email: string, password: string) {
    const response = await api.post('/auth/login', { email, password });
    // * Extract CSRF token from login response
    const newCsrfToken = response.headers['x-csrf-token'];
    if (newCsrfToken) {
      csrfToken = newCsrfToken;
    }
    return response.data;
  }

  async register(userData: any) {
    const response = await api.post('/auth/register', userData);
    return response.data;
  }

  async forgotPassword(email: string) {
    const response = await api.post('/auth/forgot-password', { email });
    return response.data;
  }

  async resetPassword(token: string, password: string) {
    const response = await api.post('/auth/reset-password', { token, password });
    return response.data;
  }

  async verifyEmail(token: string) {
    const response = await api.post('/auth/verify-email', { token });
    return response.data;
  }

  async getCurrentUser() {
    const response = await api.get('/auth/me');
    return response.data;
  }

  async logout() {
    const response = await api.post('/auth/logout');
    return response.data;
  }

  // Violation endpoints
  async searchViolations(searchType: string, searchValue: string) {
    const paramMap: { [key: string]: string } = {
      ovr: 'ovrNumber',
      plate: 'plateNumber',
      license: 'driverLicenseNumber'
    };
    
    const response = await api.get('/violations/search', {
      params: { [paramMap[searchType]]: searchValue },
    });
    return response.data;
  }

  async getViolation(id: string) {
    const response = await api.get(`/violations/${id}`);
    return response.data;
  }

  async createViolation(violationData: any) {
    const response = await api.post('/violations', violationData);
    return response.data;
  }

  async getEnforcerViolations(params?: any) {
    const response = await api.get('/violations/enforcer', { params });
    return response.data;
  }

  async updateViolation(id: string, violationData: any) {
    const response = await api.put(`/violations/${id}`, violationData);
    return response.data;
  }

  async submitDispute(id: string, disputeData: any) {
    const response = await api.post(`/violations/${id}/dispute`, disputeData);
    return response.data;
  }

  async getViolationQR(id: string) {
    const response = await api.get(`/violations/${id}/qr`);
    return response.data;
  }

  async getOverdueViolations() {
    const response = await api.get('/violations/overdue');
    return response.data;
  }

  async getViolationStatistics() {
    const response = await api.get('/violations/statistics');
    return response.data;
  }

  // Payment endpoints
  async initiatePayment(paymentData: any) {
    const response = await api.post('/payments/initiate', paymentData);
    return response.data;
  }

  async confirmPayment(confirmationData: any) {
    const response = await api.post('/payments/confirm', confirmationData);
    return response.data;
  }

  async getPayment(id: string) {
    const response = await api.get(`/payments/${id}`);
    return response.data;
  }

  async getPaymentReceipt(id: string): Promise<Blob> {
    // * Set responseType to 'blob' to handle PDF binary data
    const response = await api.get(`/payments/${id}/receipt`, {
      responseType: 'blob'
    });
    return response.data as Blob;
  }

  async getPaymentByReceipt(receiptNumber: string) {
    const response = await api.get(`/payments/receipt/${receiptNumber}`);
    return response.data;
  }

  async getUserPayments(userId?: string) {
    if (userId) {
      const response = await api.get(`/payments/user/${userId}`);
      return response.data;
    } else {
      const response = await api.get('/users/payments');
      return response.data;
    }
  }

  // User endpoints
  async getUserProfile() {
    const response = await api.get('/users/profile');
    return response.data;
  }

  async updateUserProfile(profileData: any) {
    const response = await api.put('/users/profile', profileData);
    return response.data;
  }

  async changePassword(passwordData: any) {
    const response = await api.put('/users/password', passwordData);
    return response.data;
  }

  async getUserViolations(params?: any) {
    const response = await api.get('/users/violations', { params });
    return response.data;
  }

  async getUserStatistics() {
    const response = await api.get('/users/statistics');
    return response.data;
  }

  async enable2FA() {
    const response = await api.post('/users/enable-2fa');
    return response.data;
  }

  async disable2FA() {
    const response = await api.post('/users/disable-2fa');
    return response.data;
  }

  async deactivateAccount() {
    const response = await api.delete('/users/account');
    return response.data;
  }

  // Admin endpoints
  async getAdminDashboard() {
    const response = await api.get('/admin/dashboard');
    return response.data;
  }

  async getAdminDashboardStats() {
    const response = await api.get('/admin/dashboard/stats');
    return response.data;
  }

  async getAdminUsers(params?: any) {
    const response = await api.get('/admin/users', { params });
    return response.data;
  }

  async updateUser(id: string, userData: any) {
    const response = await api.put(`/admin/users/${id}`, userData);
    return response.data;
  }

  async deactivateUser(id: string) {
    const response = await api.delete(`/admin/users/${id}`);
    return response.data;
  }

  async getAdminViolations(params?: any) {
    const response = await api.get('/admin/violations', { params });
    return response.data;
  }

  async processDispute(id: string, disputeData: any) {
    const response = await api.post(`/admin/violations/${id}/process-dispute`, disputeData);
    return response.data;
  }

  async sendReminders() {
    const response = await api.post('/admin/notifications/send-reminders');
    return response.data;
  }

  async getViolationReport(params?: any) {
    const response = await api.get('/admin/reports/violations', { params });
    return response.data;
  }

  async getPaymentReport(params?: any) {
    const response = await api.get('/admin/reports/payments', { params });
    return response.data;
  }

  async getSystemSettings() {
    const response = await api.get('/admin/settings');
    return response.data;
  }

  async updateSystemSettings(settings: any) {
    const response = await api.put('/admin/settings', settings);
    return response.data;
  }

  // Health check
  // Notification endpoints
  async getNotifications(params?: any) {
    const response = await api.get('/users/notifications', { params });
    return response.data;
  }

  async getUnreadNotificationCount() {
    const response = await api.get('/users/notifications/unread-count');
    return response.data;
  }

  async markNotificationAsRead(id: string) {
    const response = await api.put(`/users/notifications/${id}/read`);
    return response.data;
  }

  async markAllNotificationsAsRead() {
    const response = await api.put('/users/notifications/read-all');
    return response.data;
  }

  // Health check
  async healthCheck() {
    const response = await api.get('/health');
    return response.data;
  }
}

// Export singleton instance
export const apiService = new ApiService();
export default api;

